import { ComponentHooks, html, register } from "gh-components-core";
import { nothing } from "lit-html/lib/part";

import { getTheme } from "./theme";

function Button({ outline, theme, big, href }, { useMemo }: ComponentHooks) {
  const { palette } = getTheme();

  const colors = useMemo(() => {
    switch (theme) {
      case "dark": return outline ? {
        fontHoverColor: "inherit"
      } : {
        bgColor: palette.dark,
        fontColor: palette.white
      };
      case "light": return outline ? {

      } : {
        bgColor: palette.light,
        fontColor: palette.primary
      };
      case "primary": return outline ? {
        bgColor: "transparent",
        bgHoverColor: "rgba(0, 0, 0, 0.05)",
        borderColor: palette.primary
      } : {
        bgColor: palette.primary,
        bgHoverColor: palette.primaryHover,
        fontColor: palette.white
      };
      default: return {
        bgColor: "transparent",
        bgHoverColor: "rgba(0, 0, 0, 0.05)",
        fontColor: palette.primary
      };
    }
  }, [outline, theme, palette]);

  const useHover = useMemo(() =>
    colors.bgHoverColor || colors.fontHoverColor,
    [colors.bgHoverColor, colors.fontHoverColor]);

  return html`
    ${href ? html`
      <a href="${href}"><slot></slot></a>
    ` : html`
      <div><slot></slot></div>
    `}

    <style>
      :host div, :host a {
        position: relative;
        display: inline-flex;
        align-items: center;
        padding: ${big ? `1em` : `0.75em`};
        cursor: pointer;
        user-select: none;
        border-radius: 3px;
        color: ${colors.fontColor || "inherit"};
        ${colors.borderColor ? html`border-style: solid;` : nothing}

        ${colors.bgColor ? html`background-color: ${colors.bgColor};` : nothing}
        ${colors.borderColor ? html`border-color: ${colors.borderColor};border-width: 3px;` : nothing}
      }

      :host a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }

      ${useHover ? html`:host div:hover, :host div:active, :host a:hover, :host a:active {
        ${colors.bgHoverColor ? html`background-color: ${colors.bgHoverColor};` : nothing}
        ${colors.fontHoverColor ? html`color: ${colors.fontHoverColor};` : nothing}
      }` : nothing}
    </style>
  `;
}

register("gh-button", Button);
