import { html, register } from "gh-components-core";

function ListMenu() {
  return html`
    <slot></slot>

    <style>
      :host {
        position: relative;
        display: block;
        min-width: 240px;
        padding: 32px 24px 32px 0;
      }
    </style>
  `;
}

register("gh-listmenu", ListMenu);
