import { html, register } from "gh-components-core";

import "../../components/gh-contentpage";
import "../../components/gh-listmenu";

function TryPage({ route }) {
  return html`
    <gh-contentpage class="menu-page">
      <gh-listmenu>
        <gh-listmenu-item href="#try" selected="${route === "try" ? "true" : ""}">Try GrowHub Components</gh-listmenu-item>
        <gh-listmenu-item href="#try/create" selected="${route === "try/create" ? "true" : ""}">1. Create</gh-listmenu-item>
        <gh-listmenu-item href="#try/properties">2. Properties</gh-listmenu-item>
        <gh-listmenu-item href="#try/logic">3. Logic</gh-listmenu-item>
        <gh-listmenu-item href="#try/events">4. Events</gh-listmenu-item>
        <gh-listmenu-item href="#try/style">5. Style</gh-listmenu-item>
      </gh-listmenu>
      <div>
        <slot></slot>
      </div>
    </gh-contentpage>

    <style>
      :host .menu-page {
        display: flex;
      }
    </style>

  `
}

register("gh-try-page", TryPage, ["route"]);
